/*
Teaser

Article teaser. You can turn the whole teaser into a link (preferred use) or just make links from title, image, and
more link. Designed for use in grid. Number of lines in title and description on big screens can be set via LESS
variables.

Markup:
<article class="teaser">
    <h3 class="teaser-title"><a href="#">Souvisela smrem co pralesem spojench k zken uchu</a></h3>
    <div class="teaser-submitted"><a href="#">Leonardo da Vinci</a> | 6. 9. 1986</div>
    <a href="#" class="teaser-image">
        <img src="//satyr.io/800x400" alt="Picture" />
    </a>
    <div class="teaser-description">
        <p>V globln mu vrni obrovsk zdejch stoup dva stejnho reklamy vy svj hloubce disponuj, kulturnm
        by anci poslnjane ledov, pobe zdejch zajiuje, m m ona ta nimi kameni instituce, pavouka smr
        epidemi o institut tehdy ty smeten draci pak i podporovala amatrsky mocn zaloen en protat kde.
        </p>
    </div>
    <a href="#" class="teaser-more">Read more</a>
</article>

Styleguide 2.4
*/

