.teaser {
    display: block;
    clear: both;
    text-decoration: none;

    @include hover-focus() {
        text-decoration: none;

        .teaser-title {
            color: $link-hover-color;
        }
    }
}

.teaser-title {
    margin-top: 0;
}

.teaser-submitted {
    margin-bottom: map-get($spacers, 2);
    font-size: $font-size-sm;
    color: $gray-600;
}

.teaser-image {
    display: block;
    margin-bottom: map-get($spacers, 3);
}

.teaser-image img {
    @include img-fluid;
}

.teaser-description {
    margin-bottom: map-get($spacers, 3);
    color: $body-color;
}

.teaser-more::after {
    content: $teaser-more-decoration;
}

@include media-breakpoint-up(sm) {
    .teaser-submitted {
        @include text-overflow;
    }

    .teaser-description {
        overflow: hidden;
    }

    .teaser-image {
        float: left;
        max-width: 180px;
        margin-right: 1.5em;
    }
}

@include media-breakpoint-up(md) {
    .teaser-title {
        height: $teaser-title-lines * 1em * $headings-line-height;
        overflow: hidden;
    }

    .teaser-image {
        float: none;
        max-width: none;
        margin-right: 0;
    }

    .teaser-description {
        height: $teaser-description-lines * map-get($spacers, 3);
    }
}
