img,
.article-image > img {
    @include img-fluid;
}

.article-header {
    max-width: 45em;
}

.article-body {
    max-width: 35em;
}

// Float images outside the article's text boundaries.
//stylelint-disable no-descending-specificity
.article-body-floated-images-right img,
.article-body-floated-images-alternate img:nth-of-type(even) {
    @include media-breakpoint-up(sm) {
        float: right;
        clear: right;
        margin: $article-floated-image-offset-right;
    }
}

.article-body-floated-images-left img,
.article-body-floated-images-alternate img:nth-of-type(odd) {
    @include media-breakpoint-up(sm) {
        float: left;
        clear: left;
        margin: $article-floated-image-offset-left;
    }
}
//stylelint-enable
