/*
Forms

Styleguide 3.2
*/

/*
Required Fields

Adds and asterisk to all required field labels. Requires `.required` class on labels.

Markup:
<label for="name" class="required">Name</label>
<input type="text" class="form-control" id="name" name="name" required />

Styleguide 3.2.1
*/

//stylelint-disable selector-no-qualifying-type
label.required::after {
    content: ' *';
    color: $gray-300;
}

// Provide some more spacing around file input
input[type="file"] {
    margin: map-get($spacers, 2) 0;
}

input[type="file"].form-control {
    margin: 0;
}
//stylelint-enable

/*
Form Actions

Static form action designed for cancel link to return from edit form back to listing.

Markup:
<button type="submit" class="btn btn-primary btn-raised">Save and publish</button>
<div class="form-action-static">
    or
    <a href="#">Cancel</a>
</div>

Styleguide 3.2.2
*/

.form-action-static {
    position: relative;
    top: 2px; // compensate button border
    display: inline-block;
    margin-bottom: map-get($spacers, 3);
    margin-left: $padding-large-horizontal;
}
